a, a:hover {
  color: #33334d;
  text-decoration: none;
  font-weight: 600;
}

nav .logo {
  position: relative;
  float: left;
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  font-size: 22px;
  font-weight: 900;
  text-transform: uppercase;
}
nav .logo span {
  display: inline-block;
  position: relative;
  top: -8px;
  font-size: 13pt;
}
nav .logo:hover {
  background: #9494b8;
}
nav .links {
  float: right;
  margin-right: 30px;
  position: relative;
}
nav .links li {
  float: left;
  list-style: none;
}
nav .links li > a {
  position: relative;
  display: inline-block;
  padding: 0 10px;
  line-height: 30px;
  height: 30px;
}
nav .links li > a:hover {
  color: #fff;
  background: #33334d;
  border-radius: 2px;
}
nav .links li > a[class^=trigger-] {
  padding-right: 40px;
}
nav .links li > a .arrow {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 35%;
  text-align: center;
  right: 10px;
  border-width: 5px 5px 0 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.3) transparent;
}
nav .links li > a .arrow:after {
  content: "";
  border-left: 1px solid rgba(0, 0, 0, 0.15);
  top: -10px;
  left: -15px;
  position: absolute;
  height: 15px;
}
nav .links li ul {
  position: absolute;
  left: 0;
  margin: 0;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  display: none;
  overflow-y: scroll;
  height: 50vh;
}
nav .links li ul > li {
  clear: both;
  list-style: none;
  display: block;
  padding: 0 10px;
  margin: 0;
  width: 100%;
}
nav .open li ul > li:hover {
  background: #9494b8;
}
nav .open li ul > li:hover > a {
  background: #9494b8;
  color: #fff;
}
nav .open li:hover > .drop {
  display: block;
  animation: fadeInRight 0.3s ease;
  -webkit-animation: fadeInRight 0.3s ease;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

.dropdown1 {
  position: fixed;
  top: 0;
  right: 30px;
  padding-top: 30px;
  top: 200px;
}

.dropdown2 {
  position: fixed;
  top: 0;
  right: 30px;
  padding-top: 30px;
  top: 300px;

}